<template>
  <el-dialog
    class="custom-dialog"
    width="520px"
    lock-scroll
    title="禁用"
    append-to-body
    :model-value="visible"
    @close="dialogClose"
  >
    <div class="body-container">
      <div class="system-disabled">
        <img src="~@assets/images/system-disabled.png" alt="" />
        <p class="word">您的账户已被禁用</p>
        <p class="prompt">禁用原因：<span>欠费</span></p>
      </div>
    </div>
  </el-dialog>
</template>

<script lang="ts" setup>
defineProps<{
  visible: boolean;
}>();
const emits = defineEmits<{
  (e: "update:visible", value: boolean): void;
}>();
const dialogClose = () => {
  emits("update:visible", false);
};
</script>

